html,
body {
  font-family: var(--font-family);
  font-size: clamp(14px, 1.5vw, 16px);
  background-color: var(--bgColor-default);
  color: var(--fgColor-default);
  height: 100%;
  width: 100%;
  line-height: 1.5;
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;
  font-weight: 400;
}

.main-content {
  flex: 1;
  padding: 1rem;
  margin-left: var(--layout-offset-lg);
  transition: margin-left 0.3s ease;
  will-change: margin-left;

  @media (max-width: 1024px) {
    margin-left: var(--layout-offset-md);
  }

  @media (max-width: 768px) {
    margin-left: 0;

    &[data-sidebar="open"] {
      margin-left: var(--layout-offset-sm);
      padding: 1rem;
    }
  }
}

.container {
  width: 100%;
  max-width: clamp(600px, 90%, 1200px);
  margin-inline: auto;
}
